import React from 'react';
import "../Styles/Zoom.css"

class Zoom extends React.Component {

    checkIfNull = (field) => {
        if (this.props.display[field] !== null) { return (field + ": " + this.props.display[field]) }
    }

    render() {
        return (
            <div className='view'>
                <img className='zoomed-image' src={this.props.display.baseimageurl} alt='zoomed closer'></img>
                <div className='known-info'>
                    <h1>Catalogued Information</h1>
                    <p>{this.checkIfNull("fileid")}</p>
                    <p>{this.checkIfNull("imageid")}</p>
                    <p>{this.checkIfNull("date")}</p>
                    <p>{this.checkIfNull("caption")}</p>
                    <p>{this.checkIfNull("technique")}</p>
                    <p>{this.checkIfNull("lastupdate")}</p>
                    <p>{this.checkIfNull("copyright")}</p>
                    <p>{this.checkIfNull("renditionnumber")}</p>
                </div>
                <button className="button-x-close" onClick={() => { this.props.turnOff() }}>x</button>
            </div>
        )
    }
}

export default Zoom;